.buttons{
  display: flex;
  justify-content: center;
  z-index: 200;
  .btn{
    margin-left: 6px;
    margin-right: 6px;
    &:first-child{
      margin-left: 0px;
    }
    &:last-child{
      margin-right: 0px;
    }
  }
}
.btn{
  padding-left: 24px;
  padding-right: 24px;
  height: 64px;
  border-radius: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 400;
  font-size: 18px;
  line-height: 145%;
  transition: .4s;
  color: #fff;

  &.--orange{
    background-color: $orange;
  }

  &.--gray{
    background-color:  rgba(184, 184, 184, 0.4);
  }

  &.--load{
    color: #111111;
    svg{
      margin-right: 10px;
    }
  }
  
}
