.section{
  border-radius:  48px 48px 0px 0px;
  margin-top: -60px;
  padding-bottom: 180px;
  padding-top: 120px;
}
.main{
  text-align: center;
  padding-top: 64px;
  background: radial-gradient(59.89% 71.24% at 74.48% 0%, rgba(0, 255, 255, 0.8) 0%, rgba(0, 255, 187, 0) 100%), radial-gradient(61.22% 75.23% at 0% 93.7%, rgba(0, 255, 187, 0.8) 0%, rgba(0, 255, 119, 0) 100%), radial-gradient(62.47% 67.76% at 95.8% 100%, rgba(213, 0, 255, 0.7) 0%, rgba(255, 0, 170, 0) 100%, rgba(255, 0, 255, 0) 100%), #0325FF;
  padding-bottom: 120px;
}
.main__description{
  display: flex;
  justify-content: center;
  font-size: 24px;
  line-height: 125%;
  color: #fff;
  margin-bottom: 24px;
  span{
    margin-left: 20px;
    margin-right: 20px;
    position: relative;
    &:first-child{
      margin-left: 0;
    }
    &:last-child{
      margin-right: 0;
      &:after{
        display: none;
      }
    }
    &:after{
      content: "";
      position: absolute;
      left: 100%;
      margin-left: 16px;
      top: 50%;
      transform: translateY(-50%);
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background-color: #fff; 
      opacity: .5;
    }
  }
}
.main__title{
  color: #fff;
  margin-bottom: 24px;
}

.main__title-description{
  font-size: 32px;
  line-height: 140%;
  color: #fff;
  text-align: center;
  margin-bottom: 48px;
}

.main__pic{
  position: relative;
  margin-top: -64px;
  svg{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
  }
}

.main__pic-description{
  text-align: center;
  img{
    margin-bottom: 10px;
    display: inline-block;
  }
  p{
    font-weight: 500;
    font-size: 18px;
    line-height: 135%;
    color: #fff;
    opacity: .5;
  }
}

.svg-hover{
  z-index: 200;
  position: relative;
}

.main-hover-tooltip{
  opacity: 0;
}

.main-hover{
  position: relative;
  opacity: 0;
  text{
    background-color: #fff;
    position: absolute;
    top: 200px;
    
    z-index: 100;
    color: #fff;

  }
}
.main__buttons{
  position: relative;
  z-index: 300;
}

.partical{
  background-color: #fff;
  &__smalltitle{
    margin-bottom: 32px;
  }
  &__title{
    margin-bottom: 26px;
  }
}


.expect {
  background: url(../img/exp-bg.svg) no-repeat right top / auto 100%,  conic-gradient(from 90deg at 0% 100%, #0638C0 0deg, #2B57D0 90deg, #0638C0 360deg);
  &__title{
    color: #fff;
    margin-bottom: 64px;
  }
  &__list{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-right: -24px;
  }
  &__text{
    font-weight: 500;
    font-size: 30px;
    line-height: 125%;
    span{
      .--blue &{
        color: #0942E0;
      }
      .--magenta &{
        color: #C409E0;
      }
      .--purple &{
        color: #5C09E0;
      }
      .--green &{
        color: #00B893;
      }
      .--orange &{
        color: #FF7700;
      }
      .--red &{
        color: #F53527;
      }
    }
  }
  &__item{
    width: calc(33.333% - 24px);
    margin-right: 24px;
    background-color: #fff;
    box-shadow: 0px 8px 48px rgba(35, 32, 30, 0.08);
    border-radius: 32px;
    padding: 32px;
    margin-bottom: 32px;
    min-height: 460px;
    align-self: flex-start;
    &.--magenta {
      margin-top: 40px;
    }
    &.--green{
      margin-top: -80px;
    }
    &.--purple{
      margin-top: 80px;
    }
    &.--orange{
      margin-top: -40px;
    }
    
  }
}



.directors{
  position: relative;
  background-color: #fff;
  &__list-mobile{
    display: none;
  }
  &__title {
    h2{
      margin-bottom: 40px;
    }
  }
  &__list{
    padding-top: 100px;
    display: flex;
    flex-wrap: wrap;
    margin-right: -24px;
    max-height: 1080px;
    overflow: hidden;
  }

  &__item{
    display: flex;
    flex-wrap: wrap;
    margin-right: 24px;
    border-radius: 24px;
    width: calc(25% - 24px);
    min-height: 564px;
    margin-bottom: 80px;
    &.--orange {
      background: conic-gradient(from 90deg at 0% 100%, #F56505 0deg, #FF8E0A 90deg, #F56505 360deg);
    }
    &.--green {
      background: conic-gradient(from 90deg at 0% 100%, #046652 0deg, #068F73 90deg, #046652 360deg);
    }
    &.--purple {
      background: conic-gradient(from 90deg at 0% 100%, #36097D 0deg, #642CBE 90deg, #36097D 360deg);
    }
    &.--magenta {
      background: conic-gradient(from 90deg at 0% 100%, #2E1033 0deg, #3E2542 90deg, #2E1033 360deg);
    }
  }

  &__pic{
    height: calc(50% + 52px);
    display: flex;
    align-items: flex-end;
    margin-top: -52px;
    img{
      display: block;
      max-width: 200%;
      width: 100%;
      margin-bottom: -3px;
    }
  }

  &__info{
    display: flex;
    flex-wrap: wrap;
    background-color: rgba(0, 0, 0, 0.1);
    background-blend-mode: multiply;
    border-radius:  0px 0px 24px 24px;
    padding: 16px;
    width: 100%;
    min-height: 49%;
  }
  &__info-top{
    width: 100%;
    align-self: flex-start;
    margin-bottom: 16px;
  }
  &__info-bottom{
    width: 100%;
    align-self: flex-end;
    margin-bottom: 16px;
  }
  &__name{
    font-weight: 500;
    font-size: 24px;
    line-height: 125%;
    color: #fff;
    span {
      display: block;
      margin-bottom: 8px;
      .--orange &{
        color: #FFDCC2;
      }
      .--green &{
        color: #9DF6E4;
      }
      .--purple &{
        color: #DFCCFF;
      }
      .--magenta &{
        color: #F7CFD3;
      }
    }
  }
  &__text{
    font-size: 14px;
    line-height: 140%;
    .--orange &{
      color: #FFE4CC;
    }
    .--green &{
      color: #BAF5E9;
    }
    .--purple &{
      color: #E5D6FF;
    }
    .--magenta &{
      color: #FAD7DA;
    }
  }
  &__info-bottom{
    background-color: #fff;
    border-radius: 8px;
    padding: 8px 10px;
    margin-bottom: 0px;
  }
  &__topic{
    font-size: 14px;
    line-height: 140%;
    font-weight: 450;
  }
  &__link{
    font-size: 14px;
    line-height: 140%;
    color: #83888F;
  }
  &__tag{
    position: absolute;
    left: 16px;
    bottom: 16px;
    z-index: 20;
    background-color: #fff;
    height: 36px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 18px;
    padding-left: 16px;
    padding-right: 16px;
    font-size: 14px;
    line-height: 140%;
  }
  &__all{
    display: flex;
    justify-content: center;
    position: absolute;
    align-items: flex-end;
    height: 500px;
    background: linear-gradient(to top, #fff, #fff, transparent);
    left: 0;
    bottom: 60px;
    width: 100%;
    z-index: 200;
    .btn{
      color: #111111;
      margin-bottom: 100px;
    }
  }
}


.news{
  z-index: 200;
  position: relative;
  background: url(../img/exp-bg.svg) no-repeat right top / 100% auto,  conic-gradient(from 90deg at 0% 100%, #0638C0 0deg, #2B57D0 90deg, #0638C0 360deg);
  &__title{
    color: #fff;
    text-align: left;
    margin-bottom: 48px;
  }
  &__item{
    border-radius: 24px;
    overflow: hidden;
    background-color: #fff;
    display: block;
  }
  &__pic{
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 220px;
  }
  &__info{
    display: flex;
    flex-wrap: wrap;
    min-height: 196px;
    padding: 24px;
  }

  &__text{
    font-weight: 500;
    font-size: 20px;
    line-height: 140%;
    margin-bottom: 12px;
    align-self: flex-start;
    width: 100%;
  }
  &__date{
    font-size: 16px;
    line-height: 150%;
    color: #83888F;
    align-self: flex-end;
    width: 100%;
  }
  &__slider{
    .swiper-slide{
      width: 400px;
    }
  }
}

.history{
  background-color: #F4F5F6;
  z-index: 300;
  .swiper-slide {
    width: 600px;
  }
  &__title{
    text-align: left;
    margin-bottom: 48px;
  }
  &__item{
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: block;
    position: relative;
    border-radius: 24px;
    overflow: hidden;
    iframe{
      width: 100%;
      height: 400px;
    }
  }
  &__text{
    position: relative;
    font-weight: 500;
    font-size: 20px;
    line-height: 140%;
    padding: 24px;
    background-color: #fff;
    left: 0;
    bottom: 0;
    width: 100%;
  }
}

.accardeon__item{
  &:not(:last-child){
    border-bottom: 1px solid rgba(184, 184, 184, 0.4);
  }
}
.accardeon__main{
  font-weight: 500;
  font-size: 32px;
  line-height: 125%;
  padding-top: 24px;
  padding-bottom: 24px;
  font-weight: 500;
  font-size: 32px;
  line-height: 125%;
  cursor: pointer;
  &.active:after{
    transform: translateY(-50%)  rotateZ(180deg);
  }
  &:after{
    content: "";
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%) rotateZ(0);
    width: 40px;
    height: 40px;
    background: url(../img/down.svg) no-repeat center / contain;
    transition: .4s;
  }
}

.accardeon__drop{
  padding-top: 24px;
  display: none;
}


.partners{
  &__list{
    display: flex;
    flex-wrap: wrap;
    margin-right: -16px;
  }

  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    margin-right: 16px;
    width: calc(25% - 16px);
    background-color: #F4F5F6;
    border-radius: 16px;
    margin-bottom: 16px;
    height: 116px;
  }
}

.last{
  background: radial-gradient(59.89% 71.24% at 74.48% 0%, rgba(0, 255, 255, 0.8) 0%, rgba(0, 255, 187, 0) 100%), radial-gradient(61.22% 75.23% at 0% 93.7%, rgba(0, 255, 187, 0.8) 0%, rgba(0, 255, 119, 0) 100%), radial-gradient(62.47% 67.76% at 95.8% 100%, rgba(213, 0, 255, 0.7) 0%, rgba(255, 0, 170, 0) 100%, rgba(255, 0, 255, 0) 100%), #0325FF;
  padding-top: 120px;
  padding-bottom: 120px;
  &__logo{
    display: flex;
    justify-content: center;
    margin-bottom: 8px;
  }

  &__title{
    color: #fff;
    margin-bottom: 40px;
  }
}

@media(max-width: 1300px){
  .expect__text {
    font-size: 23px;
  }
}

@media(max-width: 1200px){
  .directors__item{
    width: calc(33.33% - 24px);
  }
}

@media(max-width: 980px){
  .directors__item{
    width: calc(50% - 24px);
  }
  .footer__nav{
    display: flex;
    flex-wrap: wrap;
    max-height: 400px;
    .nav__link{
      width: 50%;
      margin-bottom: 20px;
      margin-left: 0px;
      margin-right: 0px;
    }
  }
  .footer__copyright{
    display: block;
  }
  .copyright__left{
    margin-bottom: 30px;
  }
  .btn{
    font-size: 16px;
    height: 56px;
  }
  .expect__item{
    min-height: 338px;
    padding: 24px;
    margin-bottom: 24px;
  }
  .expect__text{
    font-weight: 500;
    font-size: 18px;
    line-height: 125%;
  }
  .main__description{
    font-size: 16px;
    line-height: 150%;
    margin-bottom: 16px;
  }
  h1{
    font-size: 32px;
    line-height: 125%;
    margin-bottom: 16px;
  }
  .main__title-description{
    font-size: 18px;
    line-height: 145%;
  }
  .main__pic svg{
    transform: translateY(-15%);
  }
  .header{
    justify-content: flex-start;
    height: 70px;
    padding-left: 20px;
    padding-right: 20px;
  }
  .header__nav{
    display: none;
  }
  .header__logo-moscow{
    padding-right: 16px;
    border-right: 1px solid rgba(#fff, .2);
    margin-right: 16px;
    img{
      width: 50px;
    }
    
  }
  .header__logo{
    width: 50px;
  }
  .header__burger{
    display: block;
    right: 20px;
    position: absolute;
  }
}

@media(max-width: 700px){
  .main__description{
    flex-wrap: wrap;
    span{
      width: 50%;
      margin: 0;
      margin-bottom: 4px;
      &:after{
        right: 0px;
        left: auto;
      }
      &:nth-child(2){
        &:after{
          display: none;
        }
      }
    }
  }
  .main__pic svg{
    transform: translateY(-25%);
  }
  .section{
    border-radius: 24px 24px 0 0;
    padding-bottom: 60px;
  }
  h2{
    font-size: 32px;
    line-height: 125%;
  }
  .footer{
    padding: 45px 0 40px;
  }
  .footer__top{
    display: block;
    margin-bottom: 30px;
  }
  .footer__socials{
    justify-content: flex-start;
  }
  .footer__logo{
    margin-bottom: 40px;
  }
  .footer__bottom {
    display: block;
  }
  .footer__nav{
    margin-bottom: 10px;
  }
  .footer__copyright{
    text-align: center;
    display: block;
  }
  .copyright__left{
    display: block;
    p{
      margin-bottom: 30px;
    }
  }
  .last{
    padding: 40px 0 40px;
  }
  .accardeon__main{
    font-weight: 500;
    font-size: 20px;
    line-height: 140%;
    padding-right: 40px;
  }
  .partners__list{
    margin-right: -8px;
  }
  .partners__item{
    width: calc(50% - 8px);
    margin-right: 8px;
    margin-bottom: 8px;
    height: 80px;
    img{
      max-height: 80%;
      max-width: 85%;
    }
  }
  .accardeon__main:after{
    width: 24px;
    height: 24px;
  }
  .partners{
    z-index: 400;
  }
  .history{
    padding-top: 40px;
  }
  .history .swiper-slide{
    width: 288px;
  }
  .history__item{
    iframe{
      height: 272px;
    }
    border-radius: 16px;
  }
  .history__text{
    font-size: 18px;
  }
  .history__title{
    margin-bottom: 24px;
  }
  .news{
    padding-bottom: 100px;
    padding-top: 40px;
    &__title{
      margin-bottom: 24px;
    }
    &__pic{
      height: 162px;
    }
  }
  .news__slider .swiper-slide{
    width: 288px;
  }

  .news__info{
    padding: 16px;
    min-height: 164px;
  }

  .news__text{
    font-weight: 500;
    font-size: 18px;
    line-height: 135%;
  }
  .news__date{
    font-size: 16px;
    line-height: 150%;
  }
  .directors__list{
    display: none;
  }
  .directors__list-mobile{
    padding-top: 72px;
    display: block;
    .directors__item{
      width: 288px;
      margin-right: 0px;
    }
  }
  .directors__all{
    display: none;
  }
  .directors .buttons{
    display: block;
    max-width: 336px;
    margin: 0 auto;
    .btn{
      width: 100%;
      margin: 0;
      &:first-child{
        margin-bottom: 12px;
      }
    }
  }
  .directors{
    padding-top: 40px;
  }
  .expect .expect__item{
    width: 288px;
    margin-top: 0px;
    margin: 0 auto 24px;
    &:nth-child(2n-1){
      transform: translateX(47px);
    }
  }
  .expect__text{
    font-size: 24px;
    line-height: 125%;
  }
  .expect__list{
    display: block;
  }
  .expect{
    padding-top: 40px;
    h2{
      margin-bottom: 32px;
    }
  }
  .partical__chart {
    display: flex;
    justify-content: center;
  }
  h5{
    font-size: 16px;
    line-height: 150%;
    margin-bottom: 12px;
  }
  .partical {
    padding-top: 40px;
  }
  .main__pic{
    margin-top: 0px;
  }
}


@media(max-width: 500px){
  .main__pic svg{
    transform: translateY(-32%);
  }
  .expect .expect__item{
    margin: 0;
    margin-bottom: 24px;
  }
}

@media(max-width: 400px){
  .main__pic svg{
    transform: translateY(-38%);
  }
}

@media(max-width: 370px){
  .expect .expect__item:nth-child(2n-1){
    transform: none;
  }
}