.footer{
  padding-top: 60px;
  padding-bottom: 30px;
  background-color: #120826;
}

.footer__top{
  display: flex;
  justify-content: space-between;
  margin-bottom: 60px;
}

.footer__socials{
  display: flex;
  justify-content: flex-end;
}

.socials__link{
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.1);
  display: flex;
  justify-content: center;
  align-items: center;
  &:not(:first-child){
    margin-left: 8px;
  }
}

.footer__bottom {
  display: flex;
  justify-content: space-between;
  padding-bottom: 25px;
  border-bottom: 1px solid rgba(#fff, .2);
  margin-bottom: 30px;
}

.footer__email{
  font-weight: 500;
  font-size: 18px;
  line-height: 165%;
  color: #fff;
}

.footer__copyright{
  font-size: 15px;
  line-height: 150%;
  color: #fff;
  opacity: .5;
  display: flex;
  justify-content: space-between;
  a{
    font-size: 15px;
    line-height: 150%;
    color: #fff;
  }
}
.copyright__left{
  display: flex;
  p{
    margin-right: 40px;
  }
}