.body {
  background: $color-bg;
  color: $color-text;
  font-family: $font-family;
  font-size: $font-size;
  letter-spacing: $letter-spacing;
  line-height: $line-height;
  scroll-behavior: smooth;
  letter-spacing: 0.05em;

  &.--frozen {
      overflow: hidden;
  }
}

.container {
  width: 100%;
  max-width: $container-width;
  padding-left: $container-padding-left; 
  padding-right: $container-padding-right;
  margin: 0 auto;
  position: relative;
  z-index: 50;
}

.hidden {
  display: none;
}

.content{
  position: relative;
  z-index: 10;
}

.text {
  line-height: 26px;
}


a {
  cursor: pointer;
  color: $link-color;
  text-decoration: none;
}


strong {
  font-weight: bold;
}

::placeholder {
  color: $placeholder;
  transition: .4s;
}

:focus::placeholder {
  opacity: 0;
}

::-webkit-scrollbar {
  width: 0px; 
  background: transparent;
}

img{
  max-width: 100%;
  display: block;
}

form{
  display: block;
  width: 100%;
}



h1, h2, h3, h4, h5, h6{    
  font-weight: normal;
  letter-spacing: -0.02em;
}

h1{
  font-weight: bold;
  font-size: 64px;
  line-height: 120%;
}

h2{
  font-weight: bold;
  font-size: 72px;
  line-height: 100%;
  text-align: center;
}

h5{
  text-align: center;
  font-weight: 400;
  font-size: 24px;
  line-height: 125%;
}
