/* Main Colors */
$color-base : #111111;
$color-bg : #fff;
$color-text : $color-base;
$color-overlay : $color-base;
$opacity-overlay: .4;
$link-color : $color-base;

$blue: #18274F;
$blue-gradient: conic-gradient(from 90deg at 0% 100%, #18274F 0deg, #30457B 90deg, #18274F 360deg);

$magenta: #391B3D;
$magenta-gradient: conic-gradient(from 90deg at 0% 100%, #2E1033 0deg, #3E2542 90deg, #2E1033 360deg);

$purple: #36097D;
$purple-gradient:  conic-gradient(from 90deg at 0% 100%, #36097D 0deg, #642CBE 90deg, #36097D 360deg);

$green: #046652;
$green-gradient: conic-gradient(from 90deg at 0% 100%, #046652 0deg, #068F73 90deg, #046652 360deg);

$orange: #FA7A0A;
$orange-gradient: conic-gradient(from 90deg at 0% 100%, #F56505 0deg, #FF8E0A 90deg, #F56505 360deg);





/* Fonts */
$font-family : 'Suisse Intl', sans-serif;
$font-title: $font-family;
$font-size : 14px;
$font-weight : normal;
$letter-spacing : normal;
$line-height : 1.3;
$placeholder: #AAA3B4;



/*container*/
$container-padding-left: 20px;
$container-padding-right: 20px;
$container-width: 1230px + $container-padding-left + $container-padding-right;

