.header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 90px;
  background-color:  #120826;
  padding-left: 50px;
  padding-right: 50px;
  color: #fff;
  &__burger{
    cursor: pointer;
    display: none;
  }
  &__nav{
    display: flex;
    justify-content: center;
  }
}

.nav{
  &__link{
    color: #fff;
    font-size: 16px;
    line-height: 24px;
    margin-left: 8px;
    margin-right: 8px;
    text-transform: uppercase;
  }
}

.burger__close{
  display: none;
}

.menu{
  position: fixed;
  display: none;
  left: 0;
  top: 70px;
  width: 100%;
  z-index: 200;
  background-color: rgba(4, 41, 81, 0.75);
  height: calc(100% - 90px);
  .nav__link{
    display: block;
    margin-bottom: 10px;
  }
}
.menu__panel{
  padding: 16px 24px 30px 24px;
  background-color: #120826;
}